.slider {
  font-family: "Roboto Condensed", sans-serif;
  width: 100%;
  height: 200px;
  position: relative;
  overflow: hidden;
}
.slider__slide {
  transition: all 0.4s ease-in-out;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 40vw;
  max-height: 600px;
  min-height: 200px;
  background-size: cover;
  background-position: center;
  z-index: 1;
  opacity: 0;
}

.boxSlider {
  position: relative;
  left: 18%;
  width: 64%;
  margin-top: 7%;
}

.slider__slide__text {
  position: relative;
  top: 11%;
}
.slider__slide__text button {
  transition: all 0.3s ease-in-out;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 36px;
  padding: 4px;
  display: block;
  background-color: #ebe7e778;
  font-weight: bold;
  width: 100%;
  border: none;
  font-family: ui-monospace;
}
.slider__slide__text button:hover {
  background: rgba(20, 20, 20, 0.4);
  color: #c8c8c8;
}
.slider__slide[data-active="true"] {
  -webkit-transform: scale(1);
          transform: scale(1);
  z-index: 100;
  opacity: 1;
}
.slider__next,
.slider__previous {
  transition: all 0.3s ease-in-out;
  color: rgba(255, 255, 255, 0.6);
  z-index: 200;
  position: absolute;
}
.slider__next:hover,
.slider__previous:hover {
  color: rgba(255, 255, 255, 0.8);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.slider__next {
  bottom: 20px;
  right: 20px;
}
.slider__previous {
  bottom: 20px;
  left: 20px;
}

